import React, { useState } from 'react';
import {
  Box,
  Button,
  HStack,
  VStack,
  Text,
  Icon,
  Flex,
  Heading,
  Badge,
  Card,
  CardHeader,
  CardBody,
  Grid,
  IconButton,
  useToast,
} from '@chakra-ui/react';
import { CirclePlay, Users, BookOpen, Target, Brain, BarChart3, Settings } from 'lucide-react';
import PlayDesignerOptimized from './components/football/PlayDesignerOptimized';
// Support the compiled CommonJS export of AIBrainDashboard
// eslint-disable-next-line @typescript-eslint/no-var-requires
const AIBrainDashboard = require('./ai-brain/components/AIBrainDashboard.js');

type ViewId = 'dashboard' | 'playbook' | 'ai-brain' | 'analytics';

function App() {
  const [currentView, setCurrentView] = useState<ViewId>('dashboard');
  const toast = useToast();

  const handleViewChange = (view: ViewId) => {
    setCurrentView(view);
    toast({
      title: 'Navigation',
      description: `Switched to ${view.replace('-', ' ')}`,
      status: 'info',
      duration: 1200,
      isClosable: true,
    });
  };

  return (
    <Box minH="100vh" bg="gray.50">
      {/* Header */}
      <Box bg="white" borderBottom="1px" borderColor="gray.200" px={6} py={4}>
        <Flex justify="space-between" align="center">
          <HStack>
            <Icon as={CirclePlay} color="blue.600" boxSize={8} />
            <VStack align="start" spacing={0}>
              <Heading size="lg" color="blue.600">Coach Core AI</Heading>
              <Text fontSize="sm" color="gray.600">The Standard for Coaching</Text>
            </VStack>
          </HStack>
          <HStack>
            <IconButton aria-label="Settings" icon={<Icon as={Settings} />} variant="ghost" />
          </HStack>
        </Flex>
      </Box>

      {/* Top Navigation */}
      <Box bg="white" px={6} py={3} borderBottom="1px" borderColor="gray.200">
        <HStack spacing={2} wrap="wrap">
          <Button
            size="sm"
            variant={currentView === 'dashboard' ? 'solid' : 'ghost'}
            colorScheme="blue"
            leftIcon={<Icon as={Users} />}
            onClick={() => handleViewChange('dashboard')}
          >
            Dashboard
          </Button>
          <Button
            size="sm"
            variant={currentView === 'playbook' ? 'solid' : 'ghost'}
            colorScheme="blue"
            leftIcon={<Icon as={BookOpen} />}
            onClick={() => handleViewChange('playbook')}
          >
            Playbook
          </Button>
          <Button
            size="sm"
            variant={currentView === 'ai-brain' ? 'solid' : 'ghost'}
            colorScheme="purple"
            leftIcon={<Icon as={Brain} />}
            onClick={() => handleViewChange('ai-brain')}
          >
            AI Brain
          </Button>
          <Button
            size="sm"
            variant={currentView === 'analytics' ? 'solid' : 'ghost'}
            colorScheme="teal"
            leftIcon={<Icon as={BarChart3} />}
            onClick={() => handleViewChange('analytics')}
          >
            Analytics
          </Button>
        </HStack>
      </Box>

      {/* Content */}
      <Box p={6}>
        {currentView === 'dashboard' && (
          <Grid templateColumns={{ base: '1fr', lg: '2fr 1fr' }} gap={6}>
            <VStack spacing={6} align="stretch">
              <Card>
                <CardHeader>
                  <Heading size="md">Quick Actions</Heading>
                </CardHeader>
                <CardBody>
                  <Grid templateColumns={{ base: '1fr', sm: 'repeat(2, 1fr)', md: 'repeat(4, 1fr)' }} gap={4}>
                    <Button leftIcon={<Icon as={Target} />} colorScheme="green" h="100px">AI Practice Plan</Button>
                    <Button leftIcon={<Icon as={BookOpen} />} colorScheme="blue" h="100px">Design Play</Button>
                    <Button leftIcon={<Icon as={Users} />} colorScheme="orange" h="100px">Manage Team</Button>
                    <Button leftIcon={<Icon as={BarChart3} />} colorScheme="teal" h="100px">View Analytics</Button>
                  </Grid>
                </CardBody>
              </Card>

              <Card>
                <CardHeader>
                  <Heading size="md">Highlights</Heading>
                </CardHeader>
                <CardBody>
                  <Grid templateColumns={{ base: '1fr', md: 'repeat(2, 1fr)' }} gap={4}>
                    <Box p={4} bg="blue.50" borderRadius="md">
                      <Heading size="sm" mb={1}>Upcoming Practice</Heading>
                      <Text fontSize="sm" color="gray.700">Tomorrow • 3:00 PM</Text>
                    </Box>
                    <Box p={4} bg="green.50" borderRadius="md">
                      <Heading size="sm" mb={1}>Recent Activity</Heading>
                      <Text fontSize="sm" color="gray.700">3 plays designed • Plan saved</Text>
                    </Box>
                  </Grid>
                </CardBody>
              </Card>
            </VStack>

            <VStack spacing={6} align="stretch">
              <Card>
                <CardHeader>
                  <Heading size="md">Status</Heading>
                </CardHeader>
                <CardBody>
                  <VStack align="stretch" spacing={3}>
                    <HStack justify="space-between">
                      <Text>Environment</Text>
                      <Badge colorScheme="blue">Development</Badge>
                    </HStack>
                    <HStack justify="space-between">
                      <Text>AI Suggestions</Text>
                      <Badge colorScheme="purple">Enabled</Badge>
                    </HStack>
                  </VStack>
                </CardBody>
              </Card>
            </VStack>
          </Grid>
        )}

        {currentView === 'playbook' && (
          <PlayDesignerOptimized />
        )}

        {currentView === 'ai-brain' && (
          <AIBrainDashboard />
        )}

        {currentView === 'analytics' && (
          <Card>
            <CardHeader>
              <Heading size="md">Analytics</Heading>
            </CardHeader>
            <CardBody>
              <Text color="gray.600">Analytics coming soon.</Text>
            </CardBody>
          </Card>
        )}
      </Box>
    </Box>
  );
}

export default App;

